@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  width: 170rem;
  border-radius: $blocks-border-radius;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-bottom: 10rem;
  box-shadow: $project-box-shadow;
  box-sizing: border-box;
  padding: $inner-container-padding;

  @media (max-width: $break-point-mobile) {
    display: none;
  }

  .dataBlock {
    display: flex;
    justify-content: space-between;

    .dataWrapper {
      display: flex;
      align-items: center;

      .title,
      .greenTitle {
        font-size: 3.2rem;
        font-weight: bold;

        margin: 0 0.5rem;
      }

      .greenTitle {
        color: $main-green-color;
      }

      .percent {
        font-size: 2.2rem;
        font-weight: 400;
        margin-bottom: 0;
        margin-top: 0.5rem;
      }
    }
  }

  .tableTitles {
    display: flex;
    align-items: center;
    margin-top: 5rem;
    border-bottom: 0.1rem solid #c2c1c1;
    padding-bottom: 1rem;

    .tableTitle {
      font-size: 1.8rem;
      font-weight: bold;
      margin-right: 5rem;
    }
  }

  .dataContainer {
    display: flex;
    align-items: center;
    border-radius: $blocks-border-radius;
    background-color: $text-gray-color;
    width: 100%;
    padding: 1rem 2rem;
    background-color: rgb(250, 249, 249);
    box-sizing: border-box;
    margin-top: 2rem;
    font-size: 1.8rem;
    font-weight: 200;
    position: relative;

    .arrow {
      width: 1.2rem;
      height: 1.2rem;
      margin-left: 1rem;
      cursor: pointer;
      transition: all 0.1s linear;
    }

    .arrowRotated {
      transform: rotate(90deg);
      transition: all 0.1s linear;
    }
  }

  .iconWrapper {
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0.5rem;
    border-radius: 50%;
    cursor: pointer;

    p {
      font-size: 1.8rem;
      font-weight: bold;
      color: white;
      margin: 0;
    }
    .icon {
      width: 1.4rem;
      height: 1.4rem;
      fill: white;
    }
  }

  .mappedDataContainer {
    display: flex;
    font-size: 1.8rem;
    font-weight: 200;
    box-sizing: border-box;
    padding: 1rem 2rem;
  }

  .dataHidden {
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s linear;
  }
  .dataShown {
    height: auto;
    max-height: 200rem;
    overflow: hidden;
    transition: all 0.5s linear;
  }
}
