@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 10rem;
  margin-bottom: 40rem;
  position: relative;

  @media (max-width: $break-point-mobile) {
    width: 100%;
  }

  .hidden {
    position: absolute;
    width: 0.1rem;
    height: 0.1rem;
    top: -20rem;
  }

  .infoBlock {
    display: flex;
    justify-content: space-between;

    @media (max-width: $break-point-mobile) {
      flex-direction: column-reverse;
      align-items: center;
    }

    .textBlock {
      width: 50.5rem;
      margin-left: 12rem;
      @media (max-width: $break-point-mobile) {
        width: 300px;
        margin-top: 15px;
      }

      .title1,
      .title2 {
        @include Title;
        margin-top: 0;

        color: $main-white-color;
        text-transform: uppercase;
        font-size: 5rem;

        position: absolute;
        width: 60rem;
        transform: rotate(90deg);

        @media (max-width: $break-point-mobile) {
          text-align: center;
          font-size: 22px;

          color: black;
          text-shadow: unset;
          transform: rotate(0deg);
          width: 300px;
          position: static;
          margin: 0;
        }
      }
      .title1 {
        top: 32rem;
        left: 42rem;
      }

      .title2 {
        top: 32rem;
        left: 34.3rem;
        text-shadow: 3px 0px 2px rgba(150, 150, 150, 1);

        @media (max-width: $break-point-mobile) {
          text-shadow: unset;
        }
      }

      .mainText {
        @include Text;
        height: 29rem;
        width: 45rem;
        overflow: hidden;
        margin-top: 17rem;
        line-height: 3rem;
        text-align: right;
        margin-right: 0;
        color: $button-text-color;
        @media (max-width: $break-point-mobile) {
          font-size: 16px;
          text-align: center;
          height: 300px;
          width: 100%;
          line-height: 25px;
          margin-top: 150px;
        }
      }
    }

    .videoBlock {
      @include MediaBlock;

      @media (max-width: $break-point-mobile) {
        width: 100%;
        height: 220px;
      }

      video {
        object-fit: fill;
      }
    }
  }

  .buttonBlock {
    position: relative;
    .redirectButton {
      position: absolute;
      width: 32.5rem;
      height: 10.6rem;
      top: 4rem;
      right: 34rem;
      border: 0.8rem solid $dark-text-color;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      font-size: 4rem;
      color: $button-text-color;
      font-weight: bold;
      cursor: pointer;

      a {
        text-decoration: none;
        color: $button-text-color;
      }
    }
    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .topLine,
  .bottomLine {
    position: absolute;
    left: -12rem;
    width: 69rem;
    border-top: 0.2rem solid $divider-color;
    opacity: 0.4;

    @media (max-width: $break-point-mobile) {
      width: 100%;
      left: 0;
    }
  }
  .topLine {
    top: 9rem;

    @media (max-width: $break-point-mobile) {
      top: 420px;
    }
  }

  .bottomLine {
    bottom: 10rem;

    @media (max-width: $break-point-mobile) {
      bottom: 0;
    }
  }

  .icon {
    position: absolute;
    width: 25.6rem;
    height: 25.6rem;
    top: -13rem;
    left: 12rem;

    @media (max-width: $break-point-mobile) {
      width: 104px;
      height: 110px;
      top: 320px;
    }
  }
}
