@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  @include Header;
  position: relative;
  z-index: 10;

  @media (max-width: $break-point-mobile) {
    height: 757px;
    overflow: hidden;
  }

  .motto {
    position: absolute;
    z-index: 200;
    font-weight: bold;
    text-transform: uppercase;
    top: 4.5rem;
    left: 102rem;

    @media (max-width: $break-point-mobile) {
      //display: none;
      width: 100%;
      height: 45px;
      background-color: #f3ede8;
      position: static;
      text-align: right;
      padding-top: 27px;
    }
    .upperText {
      font-size: 3.2rem;
      color: $upper-motto-color;
      line-height: 3rem;
      margin: 0;

      @media (max-width: $break-point-mobile) {
        font-size: 12px;
        color: #e0d5cb;
        margin-right: 10px;
      }
    }

    .bottomText {
      font-size: 2.6rem;
      color: $main-white-color;
      margin-top: 1.5rem;
      @media (max-width: $break-point-mobile) {
        font-size: 12px;
        color: #e0d5cb;
        margin-right: 10px;
      }
    }
  }

  .headerLogo {
    position: absolute;
    width: 14.9rem;
    height: 12.3rem;
    top: 7rem;
    left: 24.2rem;
    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .greenImage {
    position: absolute;
    width: 124.1rem;
    height: 83rem;
    top: 8rem;
    left: 35rem;

    @media (max-width: $break-point-mobile) {
      width: 479px;
      height: 339px;
      top: 57rem;
      left: 16rem;
    }
  }

  .scrollButton {
    position: absolute;
    width: 32.5rem;
    height: 10.6rem;
    top: 81.5rem;
    left: 40rem;
    border: 0.8rem solid $dark-text-color;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    font-size: 4rem;
    color: $button-text-color;
    font-weight: bold;
    cursor: pointer;

    @media (max-width: $break-point-mobile) {
      width: 177px;
      height: 42px;
      border: 3px solid $dark-text-color;
      font-size: 16px;
      top: unset;
      bottom: 40px;
      left: 20px;
      transform: translate(50%, 50%);
    }
  }

  .coinImage {
    position: absolute;

    width: 90.5rem;
    height: 56.7rem;
    top: 18rem;
    left: 90rem;

    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .StakingImage {
    position: absolute;
    width: 116rem;
    height: 79.7rem;
    left: 45rem;
    top: 11rem;

    @media (max-width: $break-point-mobile) {
      width: 516px;
      height: 379.7px;
      left: 0;
      top: 150px;
    }
  }

  .upperLine,
  .bottomLine {
    width: 69.4rem;
    height: 0;
    border-top: 0.1rem solid $header-leftPanel-background-color;
    position: absolute;
    @media (max-width: $break-point-mobile) {
      width: 90%;
      left: 60px;
    }
  }

  .upperLine {
    top: 28rem;
    @media (max-width: $break-point-mobile) {
      top: 160px;
    }
  }

  .bottomLine {
    top: 79rem;
    @media (max-width: $break-point-mobile) {
      top: unset;
      bottom: 260px;
    }
  }

  .hiddenMobile {
    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .textBlock {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    top: 17rem;
    left: 25rem;
    width: 58rem;

    @media (max-width: $break-point-mobile) {
      width: 300px;
      align-items: flex-start;
      top: 50px;
      left: 60px;
    }

    .title {
      @include Title;
      text-transform: uppercase;
      font-size: 5rem;
      color: white;
      text-shadow: -3px 0px 5px rgba(150, 150, 150, 1);

      @media (max-width: $break-point-mobile) {
        width: 200px;
        font-size: 28px;
        text-shadow: unset;
        color: black;
        font-size: 28px;
        text-transform: unset;
      }
    }

    .mainText {
      @include Text;
      width: 45rem;
      white-space: pre-wrap;
      text-align: left;
      line-height: 3rem;

      @media (max-width: $break-point-mobile) {
        width: 290px;
        font-size: 14px;
        line-height: 20px;
      }
    }
  }

  .navigation {
    position: fixed;
    display: flex;
    height: 7rem;
    width: 100%;
    background-color: $header-navigation-color;
    z-index: 100;

    @media (max-width: $break-point-mobile) {
      display: none;
    }

    ul {
      width: 17%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      list-style-type: none;
      font-size: 1.5rem;
      margin-left: -2rem;

      a {
        text-transform: uppercase;
        text-decoration: none;
        color: black;
        display: block;
        width: 10rem;
        text-align: center;
        font-weight: 400;
      }

      li {
        text-transform: capitalize;
        cursor: pointer;
      }

      .selected {
        color: $main-green-color;
      }
    }

    select {
      width: 12.5rem;
      height: 3.3rem;
      border: 0.1rem solid $select-border-color;
      background-color: $main-white-color;
      outline: none;
      position: absolute;
      right: 3rem;
      top: 2rem;
      font-size: 1.6rem;
      font-weight: 200;
      cursor: pointer;
    }
  }

  .coloredBlock {
    width: 100%;
    height: 87.7rem;
    background: linear-gradient(
      90deg,
      $header-background-color-dark,
      $header-background-color-light 50%,
      $header-background-color-dark
    );

    @media (max-width: $break-point-mobile) {
      height: 717px;
    }

    .leftBlock {
      background-color: $header-leftPanel-background-color;
      width: 6rem;
      height: 100%;
      position: relative;

      @media (max-width: $break-point-mobile) {
        width: 13.5%;
      }

      .line {
        position: absolute;
        transform: rotate(-90deg);
        width: 6.8rem;
        border-top: 0.1rem solid $main-white-color;
        right: 0;
        bottom: 2.8rem;

        @media (max-width: $break-point-mobile) {
          left: -2rem;
          bottom: 15rem;
          width: 30rem;
        }
      }

      .a1,
      .a2,
      .a3,
      .visitUsText {
        font-size: 1.4rem;
        font-weight: 400;
        text-transform: uppercase;
        color: $main-white-color;
        text-decoration: none;
        line-height: 0;
        position: absolute;
        transform: rotate(-90deg);
        left: -7.5rem;

        @media (max-width: $break-point-mobile) {
          font-size: 8rem;
          left: 1rem;
        }
      }

      .visitUsText {
        font-weight: 200;
        bottom: 16.5rem;
        left: -7.3rem;
        width: 20rem;
        text-transform: uppercase;

        @media (max-width: $break-point-mobile) {
          width: 41rem;
          bottom: 15%;
        }
      }

      .a1 {
        bottom: 52rem;
        width: 20rem;

        @media (max-width: $break-point-mobile) {
          bottom: 68%;
        }
      }
      .a2 {
        bottom: 39rem;
        width: 20rem;

        @media (max-width: $break-point-mobile) {
          bottom: 52%;
        }
      }
      .a3 {
        bottom: 26rem;
        width: 20rem;
        @media (max-width: $break-point-mobile) {
          bottom: 35%;
        }
      }
    }
  }

  .tsparticlesContainer {
    position: absolute;
    width: 100%;
    height: 87rem;
    overflow: hidden;

    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .mobileMenuButton {
    width: 48px;
    height: 48px;
    // background-color: white;
    border-radius: 10px;
    position: absolute;
    left: 12px;
    top: 12px;
    display: flex;
    z-index: 6;

    .burgerWrapper {
      margin: auto;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      div {
        width: 24px;
        height: 3px;
        background-color: $burger-line-color;
        margin: 4px;
        transform: rotate(0deg);
        transition: all linear 0.1s;
      }

      .rotatedTop {
        top: 7px;
        transform: rotate(45deg);
      }
      .rotatedBottom {
        bottom: 7px;
        transform: rotate(-45deg);
      }

      .rotatedBottom,
      .rotatedTop {
        width: 28px;
        position: relative;
        transition: all linear 0.1s;
      }
    }

    @media (min-width: $break-point-mobile + 1) {
      display: none;
    }
  }

  .mobileMenuButtonFixed {
    position: fixed;
  }

  .opened {
    background-color: transparent;
  }

  .overlay {
    width: 100%;
    height: 100%;
    background-color: white;
    opacity: 0.8;
    position: fixed;
    z-index: 4;
  }

  .mobileMenu {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden !important;
    position: fixed;
    z-index: 5;

    .languageToggle {
      display: flex;
      justify-content: center;

      .lang {
        font-size: 28px;
        font-weight: bold;
        margin: 100px 20px;
      }

      .langCurrent {
        color: $main-green-color;
      }
    }

    ul {
      list-style-type: none;
      width: 100%;
      margin-top: 120px;
      padding-left: 15px;
      .selectedMobile {
        color: $main-green-color;
      }

      li {
        display: flex;
        justify-content: flex-start;
        margin-top: 30px;
        a {
          text-decoration: none;
          color: black;
          display: block;
          text-align: center;
          text-transform: capitalize;
          font-size: 28px;
          font-weight: bold;
        }

        .link {
          font-weight: 400;
        }
      }
    }
  }
}
