@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  width: 170rem;
  border-radius: $blocks-border-radius;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-bottom: 30rem;
  box-shadow: $project-box-shadow;
  box-sizing: border-box;
  padding: $inner-container-padding;

  @media (max-width: $break-point-mobile) {
    display: none;
  }

  .tableTitles {
    display: flex;
    align-items: center;
    margin-top: 5rem;
    border-bottom: 0.1rem solid #c2c1c1;
    padding-bottom: 1rem;

    .tableTitle {
      font-size: 1.8rem;
      font-weight: bold;
      margin-right: 5rem;
    }
  }
}

.red {
  background-color: lighten(#830404, 70%) !important;
}

.green {
  background-color: lighten(#3fbf07, 59%) !important;
}
