@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  width: 140rem;
  height: 40rem;

  border: 0.4rem dashed rgb(131, 131, 131);
  position: relative;
  //z-index: 10;
  background-color: white;
  display: flex;
  flex-direction: column;
  margin: 10rem auto;
  margin-bottom: 30rem;

  @media (max-width: $break-point-mobile) {
    width: 100%;
    height: 1057px;
    border-radius: 10px;
  }

  .dashNameVisible {
    position: absolute;
    font-size: 4rem;
    font-weight: bold;
    transform: rotate(-90deg);
    text-transform: uppercase;
    left: -16rem;
    bottom: 5.5rem;
    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .upperBlock {
    display: flex;
    justify-content: space-between;
    padding: 2rem 1.5rem;
    padding-right: 2.5rem;
    padding-bottom: 0;

    @media (max-width: $break-point-mobile) {
      flex-direction: column-reverse;
      align-items: center;
      margin-bottom: 40px;
    }

    .dashName {
      font-size: 3.2rem;
      font-weight: bold;
      margin: 0;
      visibility: hidden;

      @media (max-width: $break-point-mobile) {
        font-size: 22px;
        visibility: unset;
      }
    }

    .icon {
      width: 5.2rem;
      height: 5.2rem;

      @media (max-width: $break-point-mobile) {
        width: 34px;
        height: 34px;
        margin: 10px;
      }
    }
  }

  .bottomBlock {
    display: flex;
    width: 100%;
    height: 30rem;

    @media (max-width: $break-point-mobile) {
      flex-direction: column;
      align-items: center;
      height: unset;
    }
    .leftBlock {
      flex-grow: 1;
      padding-left: 1rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-bottom: 5rem;

      @media (max-width: $break-point-mobile) {
        margin-right: 50px;
      }
    }
    .centralBlock {
      flex-grow: 1;
      display: flex;

      @media (max-width: $break-point-mobile) {
        flex-direction: column;
        align-items: center;
        margin: 40px 0 110px;
      }

      .progressDataContainer {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 25rem;
        margin-left: 5rem;

        @media (max-width: $break-point-mobile) {
          height: unset;
          margin-left: unset;
          margin-top: 20px;
        }
      }

      .progress {
        width: 26.5rem;
        height: 26.5rem;

        @media (max-width: $break-point-mobile) {
          height: 206px;
          width: 206px;
          margin: 10px;
        }
      }
    }
    .rightBlock {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: flex-end;
      position: relative;

      @media (max-width: $break-point-mobile) {
        flex-grow: unset;
      }

      a {
        text-decoration: none;
      }

      .scrollButton {
        width: 26.4rem;
        height: 6.7rem;

        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        font-size: 2rem;

        font-weight: bold;
        cursor: pointer;

        position: relative;
        right: 2.5rem;
        color: white;
        margin-top: 2rem;
        background-color: $main-green-color;

        @media (max-width: $break-point-mobile) {
          height: 42px;
          width: 280px;
          margin: 10px;
          font-size: 16px;
          right: 0;
        }
      }
    }
  }
}

.kpiWrapper {
  display: flex;
  height: 5rem;

  @media (max-width: $break-point-mobile) {
    height: unset;
    margin: 20px;
  }

  .kpiIcon {
    width: 4.8rem;
    height: 4.8rem;

    @media (max-width: $break-point-mobile) {
      width: 38px;
      height: 38px;
    }
  }

  .dataWrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 3rem;
    white-space: pre-wrap;
    @media (max-width: $break-point-mobile) {
      margin-left: 25px;
    }

    .title,
    .data {
      font-size: 2.2rem;
      margin: 0;

      @media (max-width: $break-point-mobile) {
        font-size: 17px;
      }
    }
    .title {
      font-weight: bold;
    }
    .data {
      font-weight: 400;
    }
  }
}

.hidden {
  @media (max-width: $break-point-mobile) {
    display: none;
  }
}

.smallBottomMargin {
  margin-bottom: 10rem;
}

.lowZ {
  z-index: 1;
}

.narrow {
  @media (max-width: $break-point-mobile) {
    height: 870px;
  }
}
