@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  @include helvetica-dark;
  color: $agreement-text-color;
  padding: 10rem 25rem;
  @media (max-width: $break-point-mobile) {
    padding: 10px;
    white-space: pre-wrap;
  }
  .title,
  .subTitle {
    font-size: 3.2rem;
    font-weight: bold;
    @media (max-width: $break-point-mobile) {
      font-size: 18px;
    }
  }

  .subTitle {
    font-size: 2.8rem;
    margin-left: 3rem;
    @media (max-width: $break-point-mobile) {
      font-size: 18px;
    }
  }

  .mainText,
  .subText {
    font-size: 2rem;
    font-weight: 400;
    // white-space: pre-wrap;
    @media (max-width: $break-point-mobile) {
      font-size: 15px;
    }
  }

  .subText {
    margin-left: 3rem;
  }
}
