@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  width: 170rem;
  height: 30rem;

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;

  @media (max-width: $break-point-mobile) {
    width: 100%;
    height: 90px;
    border-radius: 10px;
    justify-content: center;
  }

  .background {
    position: absolute;
    z-index: 5;
    top: -32rem;
    width: 134rem;
    height: 115rem;

    @media (max-width: $break-point-mobile) {
      display: none;
    }
  }

  .soon {
    position: absolute;
    top: -10rem;
  }

  .textWrapper {
    margin: auto;
    position: relative;
    z-index: 10;
    width: 42.1rem;
    height: 10.6rem;
    border: 0.5rem solid black;
    display: flex;
    background-color: white;
    @media (max-width: $break-point-mobile) {
      border: none;
      width: 100%;
      margin-top: 20px;
    }
    .text {
      @include Title;
      margin: auto;
      text-transform: uppercase;
      font-size: 4rem;

      @media (max-width: $break-point-mobile) {
        font-size: 22px;
      }
    }
  }
}
