@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  width: 50rem;
  height: 39.3rem;
  position: fixed;
  top: 30%;
  left: 35%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 10;
  display: flex;
  border-radius: $blocks-border-radius;
  @media (max-width: $break-point-mobile) {
    display: none;
  }

  .pop_up {
    padding: 1rem 2rem;
    border: 0.2rem solid black;
    border-radius: $blocks-border-radius;
    background-color: $main-white-color;
    box-shadow: $project-box-shadow;
    width: 50rem;
    height: 39.3rem;
    box-sizing: border-box;
    margin: auto;
    z-index: 12;

    .upperBlock {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 4rem;

      img {
        width: 5.2rem;
        height: 5.2rem;
      }

      p {
        font-size: 3.2rem;
        font-weight: bold;
        margin: 0;
      }
    }
    .dataBlock {
      display: flex;
      align-items: center;
      margin-top: 3rem;

      .leftPart,
      .rightPart {
        display: flex;
        flex-direction: column;
        width: 21rem;

        .title,
        .data {
          font-size: 1.8rem;
          white-space: pre-line;
        }

        .title {
          font-weight: bold;
          margin: 0;
        }

        .data {
          font-weight: 400;
          margin-top: 0.5rem;
        }
      }
    }

    .buttonBlock {
      .button {
        cursor: pointer;
        font-size: 2.6rem;
        font-weight: bold;
        text-transform: uppercase;
        text-align: center;
        border: 0.4rem solid black;
        width: 26.6rem;
        height: 5.7rem;

        border-radius: 0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 2.5rem;
      }
    }
  }
}
