@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  height: 100%;
  width: 100%;
  padding-bottom: 14rem;

  @media (max-width: $break-point-mobile) {
    flex-direction: column-reverse;
    margin-left: 0;
    margin-bottom: 30px;

    display: none;
  }

  .icon {
    position: absolute;
    height: 24rem;
    width: 24rem;
    top: 0;
    right: 9rem;
    @media (max-width: $break-point-mobile) {
      height: 66px;
      width: 66px;
      top: 310px;
      left: 0;
    }
  }

  .iconR {
    position: absolute;
    height: 24rem;
    width: 24rem;
    top: -10rem;
    left: 6rem;

    @media (max-width: $break-point-mobile) {
      width: 104px;
      height: 110px;
      top: 310px;
      left: 12rem;
    }
  }

  .topLine,
  .bottomLine {
    position: absolute;
    right: -17rem;
    width: 69rem;
    border-top: 0.2rem solid $divider-color;
    opacity: 0.4;

    @media (max-width: $break-point-mobile) {
      width: 100%;
      left: 0;
    }
  }

  .topLine {
    top: 23rem;

    @media (max-width: $break-point-mobile) {
      top: 370px;
    }
  }

  .bottomLine {
    bottom: 19rem;

    @media (max-width: $break-point-mobile) {
      bottom: 0;
    }
  }

  .topLineR,
  .bottomLineR {
    position: absolute;
    left: -18rem;
    width: 69rem;
    border-top: 0.2rem solid $divider-color;

    @media (max-width: $break-point-mobile) {
      width: 100%;
      left: 0;
    }
  }

  .topLineR {
    top: 13rem;

    @media (max-width: $break-point-mobile) {
      top: 420px;
    }
  }

  .bottomLineR {
    bottom: 25rem;

    @media (max-width: $break-point-mobile) {
      bottom: 30px;
    }
  }

  .imageBlock {
    @include MediaBlock;
    @media (max-width: $break-point-mobile) {
      width: 100%;
      height: 220px;
    }

    img {
      object-fit: cover;
      width: 105.5rem;
      height: 70.7rem;
      @media (max-width: $break-point-mobile) {
        width: 100%;
        height: 220px;
      }
    }
  }

  .infoBlock {
    width: 45rem;
    margin-left: 10rem;
    margin-right: 7rem;
    @media (max-width: $break-point-mobile) {
      width: 100%;
    }

    .title {
      @include Title;

      margin-top: 0;

      color: $main-white-color;
      text-transform: uppercase;
      font-size: 5rem;

      position: absolute;
      width: 60rem;
      transform: rotate(-90deg);
      text-shadow: -3px 0px 5px rgba(150, 150, 150, 1);
      top: 30rem;
      right: 28rem;
      @media (max-width: $break-point-mobile) {
        white-space: unset;
        font-size: 22px;
        text-align: center;
        margin-bottom: 100px;
        text-shadow: unset;
        color: black;
      }
    }

    .titleRevert {
      @include Title;

      margin-top: 0;

      color: $main-white-color;
      text-transform: uppercase;
      text-align: center;
      font-size: 5rem;

      position: absolute;
      left: 37rem;
      width: 50rem;
      transform: rotate(90deg);
      text-shadow: -3px 0px 5px rgba(150, 150, 150, 1);

      @media (max-width: $break-point-mobile) {
        white-space: unset;
        font-size: 22px;
        text-align: center;
        margin-bottom: 100px;
        color: black;
        text-shadow: unset;
        transform: rotate(0deg);
        width: 120rem;
        top: 240px;
      }
    }

    .marginTitle {
      @media (max-width: $break-point-mobile) {
        margin-bottom: 125px;
      }
    }

    .mainText {
      @include Text;
      height: 29rem;
      width: 45rem;
      overflow: hidden;
      margin-top: 28rem;
      line-height: 3rem;
      text-align: left;
      color: $button-text-color;

      @media (max-width: $break-point-mobile) {
        font-size: 18px;
        text-align: center;
        margin-top: 40px;
      }
    }

    .mainTextR {
      @include Text;
      height: 34rem;
      width: 45rem;
      overflow: hidden;
      margin-top: 18rem;
      line-height: 3rem;
      text-align: right;
      color: $button-text-color;

      @media (max-width: $break-point-mobile) {
        font-size: 16px;
        text-align: center;
        height: 300px;
        width: 100%;
        line-height: 25px;
        margin-top: 220px;
        color: $button-text-color;
        position: relative;
        right: 10px;
        overflow: hidden;
      }
    }
  }
}

.hidden {
  @media (max-width: $break-point-mobile) {
    display: none;
  }
}

/////////////////////////////////////////////////////// mobile ////////////////////////////////////////////

.wrapperMob {
  position: relative;
  display: flex;
  height: 100%;
  width: 100%;
  padding-bottom: 14rem;
  margin-left: 15rem;

  @media (min-width: $break-point-mobile + 1) {
    display: none;
  }

  @media (max-width: $break-point-mobile) {
    flex-direction: column-reverse;
    margin-left: 0;
    margin-bottom: 30px;
  }

  .icon {
    position: absolute;
    height: 15rem;
    width: 15rem;
    top: -13.5rem;
    left: -12rem;

    @media (max-width: $break-point-mobile) {
      width: 104px;
      height: 110px;
      top: 290px;
      left: 12rem;
    }
  }

  .topLine,
  .bottomLine {
    position: absolute;
    left: -25rem;
    width: 69rem;
    border-top: 0.2rem solid $divider-color;

    @media (max-width: $break-point-mobile) {
      width: 100%;
      left: 0;
    }
  }

  .topLine {
    top: 0rem;

    @media (max-width: $break-point-mobile) {
      top: 400px;
    }
  }

  .bottomLine {
    bottom: 17rem;

    @media (max-width: $break-point-mobile) {
      bottom: 15px;
    }
  }

  .imageBlock {
    width: 90.5rem;
    height: 56.7rem;

    @media (max-width: $break-point-mobile) {
      width: 100%;
      height: 220px;
    }

    img {
      object-fit: fill;
      width: 90.5rem;
      height: 56.7rem;

      @media (max-width: $break-point-mobile) {
        width: 100%;
        height: 220px;
      }
    }
  }

  .infoBlock {
    width: 56rem;
    margin-right: 8rem;
    @media (max-width: $break-point-mobile) {
      width: 100%;
    }

    .title {
      @include Title;

      @media (max-width: $break-point-mobile) {
        white-space: unset;
        font-size: 22px;
        text-align: center;
        margin-bottom: 100px;
      }
    }

    .marginTitle {
      @media (max-width: $break-point-mobile) {
        margin-bottom: 125px;
      }
    }

    .mainText {
      @include Text;
      color: $mainText-text-color;
      @media (max-width: $break-point-mobile) {
        font-size: 16px;
        text-align: center;
        height: 305px;
        width: 100%;
        line-height: 25px;
        margin-top: 150px;
        color: $button-text-color;
        overflow: hidden;
      }
    }
  }
}

.revert {
  display: flex;
  flex-direction: row;

  @media (max-width: $break-point-mobile) {
    flex-direction: column-reverse;
    margin-bottom: 0;
  }
}
